export default {
	data() {
		return {
			selectType: [{
				name: '全部',
				isSelect: true
			}, {
				name: '登山',
				isSelect: false
			}, {
				name: '溯溪',
				isSelect: false
			}, {
				name: '自驾游',
				isSelect: false
			}, {
				name: '登山',
				isSelect: false
			}, {
				name: '溯溪',
				isSelect: false
			}, {
				name: '自驾游',
				isSelect: false
			}],
			curFilter: '', //当前筛选
			//推荐排序
			recomFilter: [{
				name: '综合排序',
				value: 1,
			}, {
				name: '价格从高到底',
				value: 2,
			}, {
				name: '价格从低到高',
				value: 3
			}, {
				name: '销量由高到底',
				value: 4
			}, {
				name: '评分由高到底',
				value: 5
			}, {
				name: '驾驶时长由高到底',
				value: 6
			}],
			recomFilterVal: 1, //推荐排序默认值
		}
	},
	computed: {
		filterH() { //筛选列高度
			let sys = uni.getSystemInfoSync();
			return (sys.windowWidth / 750) * 540 + 'px'
		}
	},
	methods: {
		toCutType(idx) {
			this.selectType.map((v, i) => {
				if(idx == i) {
					v.isSelect = true
				}else {
					v.isSelect = false
				}
			})
		},
		selectFilter(type) {
			if(this.curFilter == type) {
				this.curFilter = ''
			}else {
				this.curFilter = type
			}
		},
		toCloseFilterbox() {
			this.curFilter = ''
		}
	}
}
